module.exports = "import React from \"react\";\nimport { DatePicker } from \"suplink-sdk\";\n\n//  日期选择器  只能在suplink内使用\n\nclass Demo extends React.Component {\n  state = {\n    value: new Date(),\n  };\n  render() {\n    return (\n      <>\n        <DatePicker />\n        <br />\n        <DatePicker format=\"YYYY-MM-DD hh:mm:ss\" />\n        <br />\n        <DatePicker format=\"YYYY-MM-DD hh:mm\" />\n        <br />\n        <DatePicker format=\"hh:mm\" />\n        <br />\n        <DatePicker value={new Date()} format=\"YYYY-MM-DD hh:mm\" disabled />\n        <br />\n        <DatePicker\n          format=\"hh:mm\"\n          value={this.state.value}\n          onChange={(value) => {\n            this.setState({\n              value: value,\n            });\n          }}\n        />\n        <br />\n        <DatePicker\n          startDate={new Date().getTime()}\n          enzzate={new Date().getTime() + 60 * 60 * 24 * 1000 * 365}\n          value={this.state.value}\n          onChange={(value) => {\n            this.setState({\n              value: value,\n            });\n          }}\n        />\n      </>\n    );\n  }\n}\n\nexport default Demo;\n"